import firebase from 'firebase';
import '../ControlPanelStyle.css';

var check;
var AddData;
var Auid;
var Duid;
var Uuid;
var AddTableData;
var DelTableData;
var UpdTableData;
export function StaffTableActionAdd(staffid, name, surname, role, company, email, days, workcompany, boss) {
  check = 0;
  var date = new Date();
  var reg_date = date.getMonth() + " " + date.getDate() + " " + date.getFullYear();
  if((staffid !== undefined)&&(name !== undefined)&&(surname !== undefined)&&(role !== undefined)&&(company !== undefined)&&(email !== undefined)&&(days !== undefined)&&(workcompany !== undefined)&&(boss !== undefined)){
    return (
      AddTableData = firebase.database().ref("Staff"),
      AddTableData.once('value', (snapshot) => {
        snapshot.forEach((childSnapshot) => {
          AddData = childSnapshot.val();
          Auid = AddData.id;
          if(AddData.staffid === staffid){
            check = 1;
          }
        });
        if(check === 0){
          var addUser = firebase.database().ref("Staff");
          var addNewUser = addUser.push();
          addNewUser.set({
              id: (Auid + 1),
              staffid: staffid,
              name: name,
              surname: surname,
              role: role,
              email: email,
              company: company,
              password: "",
              days: days,
              regDate: reg_date,
              workCompany: workcompany,
              boss: boss
          })
        }

      })
    );
  }
  else{
    alert("Yra tuščių laukelių!");
  }
}

export function StaffTableActionDel(delindex) {
  return (
    DelTableData = firebase.database().ref("Staff").orderByChild("id").equalTo(delindex),
    DelTableData.once('value', (snapshot) => {
        snapshot.forEach((childSnapshot) => {
          Duid = childSnapshot.key;  
        });
        var delUser = firebase.database().ref("Staff/" + Duid);
        delUser.remove(); 
    })
  );
}
export function StaffTableActionUpd(updindex, staffid, name, surname, role, company, email, days, workcompany, boss) {
  if((updindex !== undefined)&&(staffid !== undefined)&&(name !== undefined)&&(surname !== undefined)&&(role !== undefined)&&(company !== undefined)&&(email !== undefined)&&(days !== undefined)&&(workcompany !== undefined)&&(boss !== undefined)){
    return (
      UpdTableData = firebase.database().ref("Staff").orderByChild("id").equalTo(updindex),
      UpdTableData.once('value', (snapshot) => {
          snapshot.forEach((childSnapshot) => {
            Uuid = childSnapshot.key;
          });
          var updUser = firebase.database().ref("Staff/" + Uuid);
          updUser.update({
            staffid: staffid,
            name: name,
            surname: surname,
            role: role,
            email: email,
            company: company,
            days: days,
            workCompany: workcompany,
            boss: boss
          }); 
      })
    );
  }
  else{
    alert("Yra tuščių laukelių!");
  }
}