import React from "react";
import ReactToPdf from "react-to-pdf";
import Comps from "../companies.json";

function PDF(props){
    const ref = React.createRef();
    const duomenys = props.data;
    var msg = "";
    
    var ComDep;

    function GetComp() {
      Comps.map(function (company) {
        if((company.companyname + " " + company.department) === duomenys.company){
          ComDep = (company.companyname);
        }
        return ComDep;
      });
    }
    
    GetComp();
    if(duomenys.type === "Kasmetinės atostogos"){duomenys.type = "kasmetinių atostogų";msg = " Prašau man priklausančius atostoginius išmokėti kartu su darbo užmokesčio mokėjimu.";};
    if(duomenys.type === "Dekretinės atostogos"){duomenys.type = "dekretinių atostogų"};
    if(duomenys.type === "Tėvystės atostogos"){duomenys.type = "tėvystės atostogų"};
    if(duomenys.type === "Vaiko priežiūros atostogos"){duomenys.type = "vaiko priežiūros atostogų"};
    if(duomenys.type === "Mokymosi atostogos"){duomenys.type = "mokymosi atostogų"};
    if(duomenys.type === "Kūrybinės atostogos"){duomenys.type = "kūrybinių atostogų"};
    if(duomenys.type === "Nemokamos atostogos"){duomenys.type = "nemokamų atostogų"};
    return(
        <div className="pdf-page">
            <ReactToPdf targetRef={ref} filename={"Atostogų prašymas " + duomenys.name + ".pdf"}>
                {({toPdf}) => (
                    <button className="page-buttons" onClick={toPdf}>Atsisiųsti PDF</button>
                )}
            </ReactToPdf>
            <div style={{paddingBottom: "100px", paddingRight: "80px", width: 793.706, height: 1122.52}}>
                <div style={{width: 793.706, height: 1122.52, background: 'white'}} ref={ref}>
                    <div style={{width: 642.536, height: 920.96, padding: "100.78px 50.39px 100.78px 100.78px", display: "flex", flexDirection: "column", alignItems: "center"}}>
                        <div style={{fontSize: "15.9px", fontWeight: "bold"}}>{duomenys.role}</div>
                        <div style={{fontSize: "18.6px", fontWeight: "bold", textTransform: "uppercase", paddingBottom: "50px"}}>{duomenys.name}</div>
                        <div style={{fontSize: "16px", textTransform: "uppercase", textIndent: "-321.268px"}}>{"UAB „" + ComDep + "“"}</div>
                        <div style={{fontSize: "16px", paddingBottom: "80px", textIndent: "-321.268px"}}>{"Vadovas " + duomenys.confirmedBy}</div>
                        <div style={{fontSize: "18.6px", fontWeight: "bold", textTransform: "uppercase", paddingBottom: "40px"}}>{"Prašymas dėl " + duomenys.type}</div>
                        <div style={{fontSize: "15.9px"}}>{duomenys.date}</div>
                        <div style={{fontSize: "15.9px", paddingBottom: "70px"}}>{"Kaunas"}</div>
                        <div style={{fontSize: "15.9px", textIndent: "40px", paddingBottom: "150px"}}>{"Prašau mane leisti " + duomenys.type + " nuo " + duomenys.startdate + " iki " + duomenys.enddate + "." + msg}</div>
                        <div style={{fontSize: "15.9px", textIndent: "420px"}}>{duomenys.name}</div>
                        <div style={{fontSize: "13.3px"}}>(parašas)</div>
                    </div>
                </div>
            </div>
            
        </div>
    )
}
export default PDF;