import firebase from 'firebase';
import '../ControlPanelStyle.css';
import maildata from '../maildata.json';
import Comps from '../companies.json';

var check;
var AddData;
var Auid;
var Duid;
var Uuid;
var AddTableData;
var DelTableData;
var UpdTableData;
var ComDep;

function GetComp(cd) {
  Comps.map(function (company) {
    if(company.company === cd){
      ComDep = (company.companyname);
    }
    return ComDep;
  });
}

export function TableActionAdd(name, surname, email, days, code, company, role) {
  check = 0;
  var date = new Date();
  var reg_date = date.getMonth() + " " + date.getDate() + " " + date.getFullYear();
  let userid = () => {
    return Math.floor((1 + Math.random()) * 0x100000).toString(16);
  }
  var username = userid();
  if((name !== undefined)&&(surname !== undefined)&&(role !== undefined)&&(company !== undefined)&&(email !== undefined)&&(days !== undefined)&&(code !== undefined)){
    return (
      AddTableData = firebase.database().ref("User"),
      AddTableData.once('value', (snapshot) => {
        snapshot.forEach((childSnapshot) => {
          AddData = childSnapshot.val();
          Auid = AddData.id;
          if(AddData.code === code){
            check = 1;
          }
        });
        if(check === 0){
          GetComp(company);
          var addUser = firebase.database().ref("User");
          var addNewUser = addUser.push();
          addNewUser.set({
            id: (Auid + 1),
            name: name,
            surname: surname,
            email: email,
            days: days,
            code: code,
            password: "",
            company: company,
            regDate: reg_date,
            userid: username,
            role: role
          })
          window.Email.send({
            Host: "smtp.gmail.com",
            Username: maildata.emailname,
            Password: maildata.emailpassword,
            To: email,
            From: maildata.emailname,
            FromName: ComDep,
            Subject: "Registracija",
            Body: "Sveiki <b>" + name + " " + surname + '</b>,<br/><br/>jūs buvote pridėtas prie sistemos norint prisijungti pirmiausiai reikės prisiregistruoti.<br/>Registruotis galite <a href="https://linas-semiotas.github.io/react-project/">čia</a>, jūsų indentifikacinis kodas yra:<br/><br/><b>' + username + "</b>"
          })
          alert("Vartotojas sėkmingai pridėtas");
        }
      })
    );
  }
  else{
    alert("Yra tuščių laukelių");
  }
}

export function TableActionDel(delindex) {
  return (
    DelTableData = firebase.database().ref("User").orderByChild("id").equalTo(delindex),
    DelTableData.once('value', (snapshot) => {
        snapshot.forEach((childSnapshot) => {
          Duid = childSnapshot.key;  
        });
        var delUser = firebase.database().ref("User/" + Duid);
        delUser.remove(); 
    })
  );
}
export function TableActionUpd(updindex, name, surname, email, company, userid, role) {
  if((updindex !== undefined)&&(userid !== undefined)&&(name !== undefined)&&(surname !== undefined)&&(role !== undefined)&&(company !== undefined)&&(email !== undefined)){
    return (
      UpdTableData = firebase.database().ref("User").orderByChild("id").equalTo(updindex),
      UpdTableData.once('value', (snapshot) => {
          snapshot.forEach((childSnapshot) => {
            Uuid = childSnapshot.key;
          });
          var updUser = firebase.database().ref("User/" + Uuid);
          updUser.update({
            name: name,
            surname: surname,
            email: email,
            company: company,
            userid: userid,
            role: role
          }); 
      })
    );
  }
  else{
    alert("Yra tuščių laukelių!");
  }
}