import '../ControlPanelStyle.css';
import firebase from 'firebase';
import React, { useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import maildata from '../maildata.json';
import Comps from '../companies.json';

var ChangePass;
var ID;
var usercheck;
var username;
var Data;
var user;
var ComDep;
var check;

function GetComp(cd) {
  Comps.map(function (company) {
    if(company.company === cd){
      ComDep = (company.companyname);
    }
    return ComDep;
  });
}

let userid = () => {
  return Math.floor((1 + Math.random()) * 0x100000).toString(16);
}

export function PwChange(user, t1) {
  var newpw = userid();
  if(user === "User"){usercheck = "userid"}
  else if(user === "Staff"){usercheck = "staffid"}
  else{
    document.getElementById("change-pw").innerHTML = "Nepasirinktas darbuotojo tipas";
    return false;
  }
  if(t1 === ""){
    document.getElementById("change-pw").innerHTML = "Nepasirinktas darbuotojo vardas";
    return false;
  }
  return(
    check = 0,
    ChangePass = firebase.database().ref(user).orderByChild(usercheck).equalTo(t1),
    ChangePass.once('value', (snapshot) => {
      snapshot.forEach((childSnapshot) => {
          Data = childSnapshot.val();
          ID = childSnapshot.key
      });
      if(user === "User"){
        username = Data.userid;
      }
      if(user === "Staff"){
        username = Data.staffid;
      }
      if(t1 === username){
        var updUser = firebase.database().ref("User/" + ID);
        updUser.update({
          password: newpw
        });
        check = check + 1;
        if(check === 1){
          GetComp(Data.company);
          window.Email.send({
            Host: "smtp.gmail.com",
            Username: maildata.emailname,
            Password: maildata.emailpassword,
            To: Data.email,
            From: maildata.emailname,
            FromName: ComDep,
            Subject: "Registracija",
            Body: "Sveiki <b>" + Data.name + " " + Data.surname + '</b>,<br/><br/>jūsų slaptažodis buvo pakeistas.<br/> Jūsų naujas slaptažodis yra: <b>' + newpw + "</b> slaptažodį pasikeisti galėsite prisijungę į paskyrą."
          })
        }
        document.getElementById("change-pw").innerHTML = "Slaptažodis pakeistas";
        document.getElementById("change-pw").style.color = "green";
      }
      else{
        document.getElementById("change-pw").innerHTML = "Neteisingas prisijungimo vardas";
        document.getElementById("change-pw").style.color = "red";
      }
    })
  );  
};

const useStyles = makeStyles((theme) => ({
  formControl: {
    margin: theme.spacing(1),
    minWidth: 290,
  },
  selectEmpty: {
    marginTop: theme.spacing(2),
  },
}));

function UserType() {
  const classes = useStyles();
  const [Usertype, setUsertype] = React.useState('');

  const handleChange = (event) => {
    setUsertype(event.target.value);
    user = event.target.value;
  };
  return (
    <div>
      <FormControl className={classes.formControl}>
        <Select
          id="user-type"
          value={Usertype}
          onChange={handleChange}
          displayEmpty
        > 
          <MenuItem value="">
            <em>Pasirinkti darbuotojo tipą</em>
          </MenuItem>
          <MenuItem value={"User"}>Darbuotojas</MenuItem>
          <MenuItem value={"Staff"}>Moderatorius</MenuItem>
        </Select>
      </FormControl>
    </div>
  );
}

function ResetPassword(props) {
  var t;
  var t1;
  var pass = /^[0-9a-zA-Z]+$/;
  const [UN, setUN] = useState('');
  t1 = "";
  for(var i = 0; i < UN.length; i++){
    t = UN.charAt(i);
    if(t.match(pass)){
      t1 = t1 + t;
    }
  }
  return (
    <div className="dashboard-page">
      <h2>Slaptažodžio pakeitimas</h2>
      <div>
        {UserType()}
      </div>
      <div>
        <div className="name">Prisijungimo vardas</div>
        <input id="user-name" placeholder="Prisijungimo vardas" className="input-box" type="text" value={t1} onChange={event => setUN(event.target.value)} maxLength="20" />
      </div>
      <div id="change-pw"></div>
      <div className="buttons">
        <button id="reset-button" className="pw-button" onClick={() => PwChange(user, t1)}>Pakeisti slaptažodį</button>
      </div>
    </div>
  );
}

export default ResetPassword;